import React from 'react';

import './card.scss';

const Card = (props) => (
  <div className="col-md-2">
    <div className='card-box'>
      <div className='img-container' style={{backgroundImage: `url(https://picsum.photos/20${props.idx}/300/?random)`}}>
        <div className='text-container pl-2 pr-2'>{props.title}</div>
      </div>
    </div>
  </div>
);

export default Card;



